.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.main-container {
  margin-top: 100px;
  width: 600px;
  height: 400px;
/*   margin: auto; */
  border: 1px black solid;
}

.start-screen {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;

}

.content {
  width: 100%;
  height: 250px;
}

.title {
  text-align: center;
}

.msg {
  display: -ms-flexbox;
  display: flex;
}

.msg p {
  margin: 5px;
}

.msg button {
  width: 50%;
}

.score-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-flow: column;
      flex-flow: column;
}

#flag, #answersButtons {
  width: 50%;
  height: 100%;
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px black solid;
  display: -ms-flexbox;
  display: flex;    
}

#header, #footer {
  height: 75px;
}

#header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

#flag {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;  
}

#answersButtons {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

#answersButtons > button {
  width: 80%;
  height: 25%;
}

#flagImage {
  width: 230px;
  height: 152px;
  background-image: url(../src/img/flags.jpg);
}

#continents > input {
  margin-right: 10px;
}

#footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

#start {
  margin: 15px;
  width: 50%;
  height: 20%;
  font-size: 40px;
}




